import React, {useEffect, useState}from "react";
import ProfileContent from "../component/profileContent";
import TopMenu from "../component/topMenuComponent";
import SectionTitle from "../component/sectionTitleComponent";
import {AboutContent} from "../component/aboutContent";
import ProjectContent from "../component/projectContent/projectContent";
import { faPhone,faEnvelope } from '@fortawesome/free-solid-svg-icons';
import ExContent from "../component/experienceContent";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import AnimatedDiv from "../component/animatedComponent";
import { motion } from "framer-motion";
import { Link,Element,scroller} from 'react-scroll';

function ProfilePage(){
    const containerStyle = {
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        justifyContent: 'flex-start',
        maxWidth: '1000px',
        margin: '0 auto',
        flexGrow: '1',
        paddingLeft:'55px'
        
    }
    const sectionStyle = {
        marginBottom: '200px',
        width: '100%'
    }
    const imgStyle = {
        width: '25px', height: '25px', borderRadius:'5px',padding:'2px' 
    }
    const contactLine = {
        display: 'flex',  // 
        justifyContent: 'center',
        alignItems: 'center',
        marginTop: '25px',
      };
      
    const clickableArea = {
        display: 'inline-flex',  // 
        cursor: 'pointer',  // 
        alignItems: 'center',  // 
    };
    const email = "chuntsexj@gmail.com"
    const navStyle = {
        position: 'fixed', // 
        top: '0', // 0
        left: '0', // 0
        width: '50px', // 
        height: '100%', // 
        display: 'flex',
        flexDirection: 'column', // 
        alignItems: 'center', // 
        paddingTop: '50px', // 
        boxShadow: '2px 0 5px rgba(0, 0, 0, 0.1)', // 
        boxSizing: 'border-box',
        backgroundColor: '#080808',
        marginRight:'50px'
    };
    
    const menuItemStyle = {
        writingMode: 'vertical-rl', // 
        transform: 'rotate(180deg)', // 180
        cursor: 'pointer', // 
        color: 'white', // 
        fontSize: '18px', // 
        display: 'flex', // 
        alignItems: 'center', // 
        justifyContent: 'center', // 
        width: '100%', // 
        height: 'auto', // 
        padding: '25px 0', // 
        boxSizing: 'border-box',
    };
    const selectedMenuItemStyle = {
        ...menuItemStyle,
        backgroundColor: '#232323', // 
        borderLeft: '2px solid #0FFF9D'
    };
    const [selected, setSelected] = useState(null); // 

    const handleSetActive = (section) => {
        setSelected(section);
    };

    const handleClick = (section) => {
        //  clickOffset 
        scroller.scrollTo(section, {
            duration: 500,
            delay: 0,
            smooth: 'easeInOut',
            offset: clickOffset // 
        });
    };
    useEffect(() => {
        const handleScroll = () => {
            const sections = ['intro','about', 'project', 'experience', 'contact'];
            sections.forEach((section) => {
                const sectionElement = document.getElementById(section);
                if (sectionElement) {
                    const rect = sectionElement.getBoundingClientRect();
                    //  section 
                    if (rect.top <= window.innerHeight / 2 && rect.bottom >= window.innerHeight / 2) {
                        handleSetActive(section);
                    }
                }
            });
        };

        window.addEventListener('scroll', handleScroll);
        return () => window.removeEventListener('scroll', handleScroll);
    }, []);
    useEffect(() => {
        window.scrollTo(0, 0);
    }, []);
    const clickOffset = -100

    return(
        <div style={containerStyle}>
            <nav style={navStyle}>
            <div style={selected === 'intro' ? selectedMenuItemStyle : menuItemStyle} onClick={() => handleClick('intro')}>
                    <Link
                        to="intro" //  section  ID
                        smooth={true} // 
                        duration={500} // 
                        spy={true}
                        onSetActive={() => handleSetActive('intro')}
                        offset= {clickOffset}
                    >
                        Intro
                    </Link>
                </div>
                <div style={selected === 'about' ? selectedMenuItemStyle : menuItemStyle}onClick={() => handleClick('about')}>
                    <Link
                        to="about" //  section  ID
                        smooth={true} // 
                        duration={500} // 
                        spy={true}
                        onSetActive={() => handleSetActive('about')}
                        offset= {clickOffset}
                        
                    >
                        About
                    </Link>
                </div>
                <div style={selected === 'project' ? selectedMenuItemStyle : menuItemStyle} onClick={() => handleClick('project')}>
                    <Link
                        to="project"
                        smooth={true}
                        duration={500}
                        spy={true}
                        onSetActive={() => handleSetActive('project')}
                        offset= {clickOffset}
                    >
                        Project
                    </Link>
                </div>
                <div style={selected === 'experience' ? selectedMenuItemStyle : menuItemStyle} onClick={() => handleClick('experience')}>
                    <Link
                        to="experience"
                        smooth={true}
                        duration={500}
                        spy={true}
                        onSetActive={() => handleSetActive('experience')}
                        offset= {clickOffset}
                    >
                        Experience
                    </Link>
                </div>
                <div style={selected === 'contact' ? selectedMenuItemStyle : menuItemStyle} onClick={() => handleClick('contact')}>
                    <Link
                        to="contact"
                        smooth={true}
                        duration={500}
                        spy={true}
                        onSetActive={() => handleSetActive('contact')}
                        offset= {clickOffset}
                    >
                        Contact
                    </Link>
                </div>
            </nav>
            <header>
                <TopMenu/>
            </header>
            <Element name="intro" id="intro" style={{marginBottom:'100px'}}>
                <ProfileContent/>
            </Element>
            <Element name="about" id = "about" style={sectionStyle}>
                <SectionTitle title={'About'} lineDir={'right'} size={'50px'}/>
                <AboutContent />
            </Element>
            <Element name="project" id="project" style={sectionStyle}>
                <SectionTitle title={'Project'} lineDir={'left'} size={'50px'}/>
                <ProjectContent/>
            </Element>
            <Element name="experience" id="experience" style={sectionStyle}>
                <SectionTitle title={'Experience'} lineDir={'right'} size={'50px'}/> 
                <ExContent/>
            </Element>
            <Element name="contact" id="contact" style={{...sectionStyle,marginBottom:'300px'}}>
                
                <AnimatedDiv width="100%">
                <div className='archivo-black-regular'style={{fontSize:'80px', justifyContent:'center', display:'flex'}}>
                    Contact
                </div>
                </AnimatedDiv>
                
                <motion.div
                    style={contactLine}
                >
                    <AnimatedDiv>
                        <div style={{ display: 'inline-flex', alignItems: 'center',cursor:'default'}}>
                            <FontAwesomeIcon icon={faPhone} style={imgStyle} />
                            <div className='rubik-normal' style={{ fontSize: '22px', color: 'inherit', marginLeft:'10px' }}>
                                0450685770
                            </div>
                        </div>
                    </AnimatedDiv>
                </motion.div>
                
                
                <motion.div style={{ ...contactLine }}>
                <AnimatedDiv>
                    <motion.div
                        style={{ ...clickableArea }}
                        whileHover={{ color: '#0FFF9D' }}
                        onClick={() => window.location.href = `mailto:${email}`}
                    >
                        <FontAwesomeIcon
                        icon={faEnvelope}
                        style={{ ...imgStyle }}  // 
                        />
                        <div
                        className='rubik-normal'
                        style={{ fontSize: '22px', color: 'inherit', marginLeft: '10px' }}  // 
                        >
                        {email}
                        </div>
                    </motion.div>
                    </AnimatedDiv>
                </motion.div>    
            </Element>
            
        </div>
    )
}

export default ProfilePage